@import '@dpe/base/src/global';
@import 'bootstrap/scss/media';

// Teasers
//
// Package: `@dpe/teasers`
//
// Teasers enhance the [Bootstrap media object](https://getbootstrap.com/docs/4.1/layout/media-object/)
// to improve their responsive behaviour and styling.
//
// Style guide: 7.teasers

// Large teaser
//
// Add the `.teaser-lg` class to make a larger teaser that is horizontal when space allows.
//
// .teaser-rev - Reverse the content order on large screens with `.teaser-rev`
//
// Markup: teaser-large.twig
//
// Style guide: 7.teasers.large

// Small teaser
//
// Add the `.teaser-sm` class to create a smaller, vertical teaser. Small teasers are designed
// to be grouped together into [listings](/components-library/dist/styleguide/section-8.html#kssref-8-teasers-list).
//
// Markup: teaser-small.twig
//
// Style guide: 7.teasers.small

// Category teaser
//
// Add the `.teaser-category` class to the small teaser, and adjust the image classes
// to get the category teaser. Category teasers are also designed to be grouped together into
// [listings](/components-library/dist/styleguide/section-8.html#kssref-8-teasers-category).
//
// Markup: teaser-category.twig
//
// Style guide: 7.teasers.small-category

// Card teaser
//
// Add the `.teaser-card` class to the small teaser to get a card.
// Card teasers are also designed to be grouped together into
// [listings](/components-library/dist/styleguide/section-8.html#kssref-8-teasers-card).
//
// Markup: teaser-card.twig
//
// Style guide: 7.teasers.small-card

%teaser {
  flex-direction: column;
  align-items: inherit;

  * {
    // Fix for IE11 incorrectly shrinking things.
    flex-shrink: 0;
  }

  .media-img {
    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }
    margin-bottom: $spacer-l;
  }

  .media-body {
    display: flex;
    flex-direction: column;

    p {
      margin-bottom: $spacer-m;
    }

    > :last-child {
      margin-top: auto;
    }
  }
}

.teaser {
  &-lg {
    @extend %teaser;
    @include media-breakpoint-up(lg) {
      flex-direction: row;
      align-items: center;
    }
    max-width: $container-width-xl;

    .media-img {
      @include media-breakpoint-up(lg) {
        margin-right: $spacer-m;
      }
      flex: 1;
    }

    .media-body {
      @include media-breakpoint-up(lg) {
        margin-left: $spacer-m;
      }
      @include media-breakpoint-up(xl) {
        padding-left: $spacer-ml;
      }

      p {
        @include media-breakpoint-up(lg) {
          font-size: $text-l;
        }
        font-size: $text-m;
      }
    }
  }

  &-sm {
    @extend %teaser;
    max-width: $container-width-sm;

    .media-img {
      @include media-breakpoint-up(lg) {
        margin-bottom: $spacer-l;
      }
    }

    .media-body {
      h2 {
        @include media-breakpoint-up(lg) {
          font-size: $heading-m;
        }
        font-size: $heading-s;
      }

      &,
      p {
        @include media-breakpoint-up(lg) {
          font-size: $text-s;
        }
        font-size: $text-xs;
      }
    }
  }

  &-rev {
    @include media-breakpoint-up(lg) {
      .media-img {
        order: 2;
      }

      .media-body {
        order: 1;
      }

      &.teaser-lg {
        .media-img {
          margin-right: 0;
          margin-left: $spacer-m;
        }

        .media-body {
          margin-left: 0;
          margin-right: $spacer-m;
        }
      }

      &.teaser-sm {
        .media-img {
          margin-bottom: 0;
        }
      }
    }
    @include media-breakpoint-up(xl) {
      &.teaser-lg .media-body {
        padding-left: 0;
        padding-right: $spacer-ml;
      }
    }
  }

  &-category {
    .media-img {
      margin-bottom: $spacer-m;
    }

    .media-body {
      h2 {
        margin-bottom: $spacer-m;
      }
    }
  }

  &-card {
    border-radius: $border-radius;
    border: $border-style;
    padding: $spacer-xs;

    .media-body {
      padding: 0 $spacer-ml 0;
      font-size: $text-xs;
    }

    .teaser-breaker {
      padding: $spacer-sm;
      margin-left: -($spacer-ml + $spacer-xs);
      margin-right: -($spacer-ml + $spacer-xs);
      margin-bottom: -$spacer-xs;
    }
  }
}

